.course-center-details {
  padding-top: 60px;
  width: 100%;
  background: #F6F7FB;
}
.course-center-details .preview-course {
  line-height: 40px;
}
.course-center-details .preview-course li span:nth-child(1) {
  margin-right: 40px;
  width: 100px;
  display: inline-block;
}
.course-center-details .operation-pic .ant-card {
  border-radius: 5px;
}
.course-center-details .operation-pic .ant-card .ant-card-body {
  padding: 0;
}
.course-center-details .operation-pic .ant-card:nth-of-type(2) {
  overflow-y: auto;
  height: calc(100vh - 290px);
}
.course-center-details .operation-pic .ant-card:nth-of-type(2)::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 5px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.course-center-details .operation-pic .ant-card:nth-of-type(2)::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  background: #ccc;
}
.course-center-details .operation-pic .ant-card:nth-of-type(2)::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  border-radius: 10px;
  background: #eee;
}
.course-center-details .operation-pic .center-top {
  background: #F6F7FB;
  border: none;
}
.course-center-details .operation-pic .center-top .center-title {
  margin: 30px 0 26px 0;
  width: 100%;
}
.course-center-details .operation-pic .center-top .center-title .center-title-img {
  margin-right: 15px;
  width: 134px !important;
  height: 86px;
  background: url(../../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.course-center-details .operation-pic .center-top .center-title .center-title-img img {
  width: 100%;
  height: 100%;
}
.course-center-details .operation-pic .center-top .center-title .title1 {
  font-size: 20.6874px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 10px;
}
.course-center-details .operation-pic .center-top .center-title .title2 {
  font-size: 15.7618px;
  color: #2A283D;
  opacity: 0.4;
  margin-bottom: 10px;
}
.course-center-details .operation-pic .center-top .center-title .title2 span {
  margin-right: 20px;
}
.course-center-details .operation-pic .center-top .center-title .title3 {
  margin-bottom: 10px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .span1 {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.4;
}
.course-center-details .operation-pic .center-top .center-title .title3 .span2 {
  font-size: 12px;
  color: #2A283D;
  opacity: 0.4;
  margin-left: 15px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content {
  width: 300px;
  margin-left: 20px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content .ant-progress-inner {
  background: #EAEAEA;
  border-radius: 5.91537px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content .ant-progress-success-bg,
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content .ant-progress-bg {
  background: linear-gradient(90deg, #1BDDEC 0%, #07BDF9 100%);
  box-shadow: 0px 4px 8px rgba(37, 136, 191, 0.34);
  border-radius: 5.73466px;
}
.course-center-details .operation-pic .center-top .center-title .center-btn {
  text-align: right;
  position: absolute;
  right: 25px;
  bottom: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .ant-btn {
  /*width: 56px;
                        height: 17px;
                        font-size: 14px;*/
  color: #2A283D;
  background: #3BB9FF;
  border-radius: 5px;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .ant-btn span {
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #FFFFFF;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-right {
  display: inline-block;
  margin-left: 15px;
  margin-right: 20px;
  vertical-align: middle;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-right img {
  box-sizing: border-box;
  display: inline-block;
  margin: -5px 5px 0;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-right .right-p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  display: inline-block;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-back {
  border: 1px solid #3E78ED;
  color: #3E78ED;
  padding: 0px 18px;
  background: #ffffff !important;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-back span {
  color: #3E78ED !important;
}
.course-center-details .operation-pic .center-top .ant-card-body {
  background: #ffffff;
  border-radius: 5px;
  padding: 0 20px!important;
  display: flex;
}
.course-center-details .operation-pic .center-content {
  margin: 30px 30px 30px 54px;
}
.course-center-details .operation-pic .center-content .center-content-left {
  padding-right: 27px;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs {
  margin: 10px 15px 30px 30px;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs .ant-tabs-bar {
  margin: 0 0 30px 0;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs-tab {
  color: #2A283D;
  font-size: 18px;
  opacity: 0.5;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs-tab-active {
  opacity: 1 !important;
}
.course-center-details .operation-pic .center-content .center-content-left .tab3 .centers {
  color: #2A283D;
  height: 100%;
  line-height: 26px;
}
.course-center-details .operation-pic .center-content .center-content-left .tab3 .centers dl {
  padding: 25px;
  box-shadow: 1px 1px 10px rgba(224, 224, 224, 0.5);
  border-radius: 5px;
  margin-bottom: 20px;
  min-height: 50px;
}
.course-center-details .operation-pic .center-content .center-content-left .tab3 .centers dl dt {
  border-left: 3px solid #3E78ED;
  line-height: 1;
  padding-left: 10px;
  margin-bottom: 20px;
}
.course-center-details .operation-pic .center-content .center-content-left .tab3 .centers dl dd {
  text-indent: 2em;
}
.course-center-details .operation-pic .center-content .fixed-center {
  position: fixed;
  right: 26px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right {
  margin: 0 18px 18px 8px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-title {
  font-size: 22px;
  line-height: 120.7%;
  color: #2A283D;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-img {
  text-align: right;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-img span {
  color: #00BBF8;
  font-size: 14px;
  line-height: 120.7%;
}
.course-center-details .operation-pic .center-content .fixed-center .center-right-content {
  margin: 32px 0 28px 32px;
  cursor: pointer;
}
.course-center-details .operation-pic .center-content .fixed-center .center-right-content .center-content-right-content-img {
  min-width: 66px;
  min-height: 73px;
  background: url(../../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
  margin-right: 10px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-right-content .center-content-right-content-img img {
  width: 100%;
  height: 100%;
  min-width: 66px;
  min-height: 73px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-right-content .right-title1 {
  font-size: 18.0844px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 7px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-right-content .right-title2 {
  color: #2A283D;
  font-size: 14px;
  opacity: 0.4;
  margin-bottom: 4px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-right-content:hover .right-title1 {
  color: #3E78ED;
}
.paper-wrap-lh div > img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.paper-wrap-lh .ant-col > div {
  height: 160px;
}
.paper-wrap-lh .ant-col h4 {
  font-weight: 500;
  font-size: 18px;
  margin: 10px 0;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.paper-wrap-lh .ant-col p {
  color: #888;
}
.paper-wrap-lh .ant-col p i {
  margin-left: 8px;
}
.paper-wrap-lh .ant-col p span {
  margin-right: 8px;
  font-size: 12px;
}
.paper-wrap-lh .ant-col p img {
  vertical-align: -1px;
  margin-right: 2px;
}
.paper-center-div-l:nth-child(5n) {
  margin-right: 7% !important;
}
